import React, { useState } from 'react';

const RunCodeApp = ({setOutput}) => {

    const [isDisabled, setIsDisabled] = useState(false);
    const [input, setInput] = useState("");

    const change = (e) => {
        setInput(e.target.value);
    }

    const clear = (e) => {
        if (window.confirm("¿Desea limpiar la consola?")) {
            setInput("");
        }
    }

    // This function compile the code
    const run = async (e) => {
        e.preventDefault();


        if (input !== "") {

            setIsDisabled(true);

            let data = encodeURIComponent(input);
            let url = 'http://localhost:8080/code/?data=' + data;

            setOutput("");
            setOutput("... please wait a moment");

            try {
                const res = await fetch(url, { method: 'GET', headers: { 'content-type': "text/plain" } });
                const data = await res.text();
                setOutput("");
                setOutput(data);

                setIsDisabled(false);

            } catch (error) {
                setIsDisabled(false);
                setOutput("Error de conexión!\n");
                console.error(error);
            }
        } else {
            setOutput("You need to write something to run code!\n");
        }
    }

    return (
        <>
            <div id="runContainer" className="form-group" style={{ marginBottom: '5px' }}>
                <textarea className="form-control" id="runCode" placeholder="Custom code = new Custom(); /* Exemple of use */" value={input} onChange={change}></textarea>
                <div className="options">
                    <button onClick={run} className="btn btn-secondary" id="run" disabled={isDisabled ? true : false}>Run</button>
                    <button onClick={clear} className="btn btn-danger" id="clearCode" style={{ marginLeft: '5px' }}>Clear</button>
                </div>
            </div>
        </>
    );
}

export default RunCodeApp;