import React from 'react';

const About = ({team, members, projectSite }) => {
    
    return (
        <div className="modal fade" id="information" aria-hidden="true">
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="titulo">{team}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <div id="cuerpo" className="text-center">
                            <h4>Universidad Nacional</h4>
                            <h5>Escuela de informática</h5>
                            <h6>Paradigmas de programación</h6>
                            <h6>Dr. Carlos Loría Sáenz</h6>
                            <table className="table table-borderless text-center">
                                <thead>
                                    <tr>
                                        <th scope="col">Estudiantes </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {members.map(e => <tr key={e.id}><td>{`${e.firstName} ${e.surnames}`}</td></tr>)}
                                </tbody>
                            </table>
                            <div id="metadata">
                                <a href={projectSite} title="Get the code!">Respositorio GitHub</a>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;