import React from 'react';
import InputCodeApp from './InputCode';
import RunCodeApp from './RunCode';

const Input = ({setOutput}) => {

    return (
        <>
            <div className="Code_Section" id="input">
                <InputCodeApp setOutput={setOutput} />
                <RunCodeApp setOutput={setOutput} />
            </div>
        </>
    );
}

export default Input;