import React, { useState } from 'react';

import Input from './Input';
import OutputCodeApp from './OutputCode';


const Enano = () => {

    const [output, setOutput] = useState('');

    return (
        <>
            <Input setOutput={setOutput} />
            <OutputCodeApp setOutput={setOutput} output={output} />
        </>
    );
}

export default Enano;