import React, { useState } from 'react';
import About from './About';

const Header = () => {

    const [data, setData] = useState('');
    const [isEnabled, setIsEnabled] = useState(false);

    const nanoConnect = async () => {
        try {
            const res = await fetch('http://localhost:8080/about');
            const data = await res.json();
            setData(data);
            console.log(data);
            
            // To check error connections from database
            if (data.hasOwnProperty('Error')) {
                window.alert("Error de conexión a la base de datos!");
                setIsEnabled(false);
            } else {
                setIsEnabled(true);
            }

        } catch (error) {
            window.alert("Error de conexión!");
            console.error(error);
            setIsEnabled(false);
        }
    }

    return (
        <>
            <div className="logo">
                <img src="images/Logo.png" alt="logo" />
            </div>
            <nav className="navbar navbar-expand-lg navbar-dark ml-auto" id="menu">

                <button className="navbar-toggler ml-auto" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavDropdown">

                    <ul className="navbar-nav ml-auto">
                        <div id="info">
                            <a onClick={nanoConnect} href="/#" className="dropdown-item" id="about" data-toggle="modal" data-target="#information">About</a>
                        </div>
                    </ul>
                </div>
            </nav>
            <hr />

            {isEnabled ? <About team={data.team.code} members={data.team.members} projectSite={data.projectSite} /> : <About team={''} members={[]} projectSite={''} />}
        </>
    );
}

export default Header;