import React from 'react';

const OutputCodeApp = ({ setOutput, output }) => {

    const clear = (e) => {
        e.preventDefault();
        if (window.confirm("¿Desea limpiar la consola?")) {
            setOutput("");
        }
    }

    return (
        <div className="Code_Section" id="output">
            <div className="form-group" style={{ marginBottom: '5px' }}>
                <label>The results will appear here!</label>
                <textarea className="form-control" id="outputCode" readOnly value={output}></textarea>
                <div className="options">
                    <button onClick={clear} className="btn btn-secondary" id="clearConsole">Clear console</button>
                </div>
            </div>
        </div>
    );
}

export default OutputCodeApp;

