import React, { useState } from 'react';

const InputCodeApp = ({ setOutput }) => {

    const [isDisabled, setIsDisabled] = useState(false);
    const [input, setInput] = useState("");

    const change = (e) => {
        setInput(e.target.value);
    }

    const clear = (e) => {
        if (window.confirm("¿Desea limpiar la consola?")) {
            setInput("");
        }
    }

    // This function compile the code
    const compile = async (e) => {
        e.preventDefault();

        if (input !== "") {

            setIsDisabled(true);

            let url = 'http://localhost:8080/code';
            setOutput("");
            setOutput("*** Javacc API as Web Service ****\n... compiling the code\n\n");

            try {
                const res = await fetch(url, { method: 'POST', headers: { 'content-type': "text/plain" }, body: input });
                const data = await res.text();
                setOutput(e => e + data);
                setIsDisabled(false);

            } catch (error) {
                setIsDisabled(false);
                setOutput("Error de conexión!");
                console.error(error);
            }
        } else {
            setOutput("Your need to write something to compile code!\n");
        }
    }

    return (
        <>
            <div id="inputCode" className="form-group" style={{ marginBottom: '5px' }}>
                <label>Write Java code here!</label>
                <textarea className="form-control" id="submitCode" value={input} onChange={change}></textarea>
                <div className="options">
                    <button onClick={compile} className="btn btn-secondary" id="compile" disabled={isDisabled ? true : false}>Compile</button>
                    <button onClick={clear} className="btn btn-danger" id="clearCode" style={{ marginLeft: '5px' }}>Clear</button>
                </div>
            </div>
        </>
    );
}

export default InputCodeApp;


